/* Info Section */
.info-section {
    padding: 0 32px;
    text-align: center;
    background-color: #E6F7E9; /* Light green background */
}

.info-title-content {
    margin: 64px 0 128px 0;
}

.info-title {
    font-family: 'Poppins', sans-serif;
    font-size: 40px;
    font-weight: bold;
    color: #285C2F; /* Dark green title color */
}

.info-title > span {
    position: relative;
}

.info-title span::before {
    content: '';
    position: absolute;
    width: 75%;
    height: 4px;
    background-color: #98E165; /* Lighter green underline color */
    border-radius: 8px;
    left: 0;
    bottom: -12px;
}

.info-description {
    margin: 64px;
    text-align: center;
    color: #4A4A4A; /* Dark gray description text color */
    font-family: 'Rubik', sans-serif;
    font-size: 18px;
    letter-spacing: .8px;
    line-height: 1.8rem;
}

/* Cards */
.info-cards-content {
    margin: 64px 0;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 32px;
}

.info-cards {
    height: auto;
    border: 2px solid #98E165; /* Light green border */
    border-radius: 12px;
    background-color: white;
    position: relative;
}

.info-card-title {
    margin: 32px 0 40px 0;
    color: #285C2F; /* Dark green title color */
    font-family: 'Poppins', sans-serif;
    font-size: 28px;
    font-weight: bold;
    letter-spacing: .8px;
}

.info-card-description {
    margin: 24px;
    color: #4A4A4A; /* Dark gray description text color */
    font-family: 'Rubik', sans-serif;
    font-size: 18px;
    letter-spacing: .8px;
    line-height: 1.5rem;
}

.info-card-icon {
    position: absolute;
    top: -24px;
    left: 44%;
    padding: 10px;
    background-color: #98E165; /* Light green background for icon */
    border-radius: 30px;
}

.info-fa-icon {
    color: #285C2F; /* Dark green icon color */
    font-size: 24px;
}

/* Responsive */
@media screen and (max-width: 1000px) {
    .info-description {
        margin: 64px 32px;
    }
    .info-cards-content {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media screen and (max-width: 700px) {
    .info-description {
        margin: 64px 8px;
    }
    .info-cards-content {
        grid-template-columns: repeat(1, 1fr);
    }
}
